import React, { Component } from 'react'
import {Card, CardDeck } from 'react-bootstrap';
import 'react-bootstrap';
export default class Services extends Component {
    render() {
        return (
            <div className="container">
               <CardDeck>
  <Card>
    <Card.Img variant="top" src="https://mdbootstrap.com/img/Mockups/Lightbox/Thumbnail/img%20(67).jpg" />
    <Card.Body>
      <Card.Title>Card title</Card.Title>
      <Card.Text>
        This is a wider card with supporting text below as a natural lead-in to
        additional content. This content is a little bit longer.
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <small className="text-muted">Mode Detailt</small>
    </Card.Footer>
  </Card>
  <Card>
    <Card.Img variant="top" src="http://aaryanapps.com/wp-content/uploads/2014/03/Business-Card-Mockup-15-Free-Version.jpg" />
    <Card.Body>
      <Card.Title>Card title</Card.Title>
      <Card.Text>
        This card has supporting text below as a natural lead-in to additional
        content.{' '}
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <small className="text-muted">Mode Detailt</small>
    </Card.Footer>
  </Card>
  <Card>
    <Card.Img variant="top" src="https://mdbootstrap.com/img/Photos/Horizontal/Nature/4-col/img%20(42).jpg" />
    <Card.Body>
      <Card.Title>Card title</Card.Title>
      <Card.Text>
        This is a wider card with supporting text below as a natural lead-in to
        additional content. This card has even longer content than the first to
        show that equal height action.
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <small className="text-muted">Mode Detailt</small>
    </Card.Footer>
  </Card>
</CardDeck>
            </div>
        )
    }
}
