import React, { Component } from 'react'
import 'react-bootstrap'
import {Carousel} from 'react-bootstrap'

export default class Home extends Component {
    
    render() {
        return (
            <div style={{height:400}} >
              <Carousel>
  <Carousel.Item>
    <img
      className="d-block w-100"
      src="https://www.aysw.com.mm/wp-content/uploads/2018/11/slider-img-1903x749.jpg"
      alt="First slide"
    />
    <Carousel.Caption>
      <h3>First slide label</h3>
      <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
    </Carousel.Caption>
  </Carousel.Item>
  <Carousel.Item>
    <img
      className="d-block w-100"
      src="http://www.hislider.com/showcase/sky/dataimages/3.jpg"
      alt="Third slide"
    />

    <Carousel.Caption>
      <h3>Second slide label</h3>
      <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
    </Carousel.Caption>
  </Carousel.Item>
  <Carousel.Item>
    <img
      className="d-block w-100"
      src="https://www.absolutetrekking.com/themes/img/slider-img-3.jpg"
      alt="Third slide"
    />

    <Carousel.Caption>
      <h3>Third slide label</h3>
      <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
    </Carousel.Caption>
  </Carousel.Item>
</Carousel>
            </div>
        )
    }
}
